/*Flip Proceso de Inscripción*/
* {
  padding: 0;
  margin: 0;
}
#card {
  max-width: 100%;
  perspective: 1000px;
  margin: 20px;
}
#card, #flip-card {
  position: relative;
  width: 285px;
  height: 425px;
}
#flip-card {
  transition: transform .5s ease-in-out;
  transform-origin: 50% 50%;
  transform-style: preserve-3d;
}
#flip-card:Hover {
  transform: rotateY(180deg);
}
#flip-card-front, #flip-card-back {
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}
#flip-card-front, #flip-card-back {
  backface-visibility: hidden;
}
#flip-card-back {
  box-shadow: 0 0 5px rgb(230,230,230);
  background-color: #EDF2F7;
  transform: rotateY(180deg);
}
#flip-card-front {
  box-shadow: 0 0 5px rgb(22,22,22);
 background: #253d95;
  opacity: 50%;
  color: white;
}
